/* Hide scrollbar htmlFor Chrome, Safari and Opera */

.long-sub-1 ul::-webkit-scrollbar {
    display: none;
    background-color: #dfdfdf;
    width: 1px;
    height: 1px;
}

.sub-first-col::-webkit-scrollbar {
    display: none;
}

.long-sub-2 ul::-webkit-scrollbar,
.long-sub-1 ul::-webkit-scrollbar-button,
.long-sub-2 ul::-webkit-scrollbar-button {
    background-color: #fff;
    display: none;
}

.long-sub-1 ul li::-webkit-scrollbar,
.long-sub-2 ul li::-webkit-scrollbar,
.long-sub-1 ul li::-webkit-scrollbar-button,
.long-sub-2 ul li::-webkit-scrollbar-button {
    display: none;
}


/* Hide scrollbar htmlFor IE, Edge and Firefox */

.long-sub-1 ul,
.long-sub-2 ul {
    -ms-overflow-style: none;
    /* IE and Edge */
    scrollbar-width: none;
    /* Firefox */
}

