/* Country Card */

.country-card {
    width: 100%;
    height: 300px;
    overflow: hidden;
    border-radius: 10px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all .25s ease;
    backface-visibility: hidden;
    margin: none;
}

.country-card:hover {}

.country-card:hover:after {}

.country-card:hover .con-text p {
    margin-bottom: 0px;
    opacity: 1;
}

.country-card:hover img {
    transform: scale(1.25);
}

.country-card:after {
    width: 100%;
    height: 100%;
    content: "";
    left: 0px;
    bottom: 0px;
    position: absolute;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 10%, rgba(0, 0, 0, 0.8) 100%);
    z-index: 20;
    transition: all .25s ease;
}

.country-card img {
    height: 100%;
    z-index: 10;
    transition: all .25s ease;
}

.country-card .con-text {
    z-index: 30;
    position: absolute;
    bottom: 30px;
    color: #fff;
    padding: 20px;
    padding-bottom: 30px;
}

.country-card .con-text p {
    font-size: .8rem;
    opacity: 0;
    margin-bottom: -170px;
    transition: all .25s ease;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex-direction: column;
}

.country-card .con-text p button {
    padding: 7px 17px;
    margin-top: 10px;
    margin-left: auto;
    cursor: pointer;
    font-size: .75rem;
    outline: none;
}

