@keyframes faq-answer-slide {
    0% {
        opacity: 0;
        max-height: 0;
    }

    100% {
        opacity: 1;
        max-height: 500px;
    }
}

.faq{
    padding: 20px 0px;
    border-bottom: 2px solid #cccccc;
    cursor: pointer;
}
.faq .faq-question{
    position: relative;
    font-size: 1.2em;
    font-weight: 900;
    padding-right: 80px;
    transition: all 0.4s ease;
}
.faq .faq-answer{
    margin-top: 20px;
    opacity: 0;
    max-height: 0;
    overflow-y: hidden;
    transition: all 0.4s ease-out;

}
.faq .faq-question::after{
    font-family: 'Material Icons';
    content: 'expand_more';
    position: absolute;
    top: 50%;
    right: 0px;
    width: 30px;
    height: 30px;

    transform: rotate(0deg);
    transition: transform 0.4s linear;

}
.faq.open .faq-question{
margin-bottom: 15px;
}

.faq.open .faq-question::after{
transform: translateY(-50%) rotate(180deg);
}
.faq.open .faq-answer{
max-height: 1000px;
opacity: 1;
